.game {
  text-align: center;
  font-family: sans-serif;
  margin-top: 30px;
}

.board {
  display: inline-block;
}

.row {
  display: flex;
}

.square {
  width: 60px;
  height: 60px;
  font-size: 24px;
  margin: 2px;
  cursor: pointer;
}

.reset {
  margin-top: 20px;
  padding: 8px 16px;
  cursor: pointer;
}
